import React, { forwardRef } from 'react'
import { get } from 'lodash'
import { useMeasure } from "react-use";

import Box from '../components/Box'
import Flex from '../components/Flex'
import Link from '../components/Link'
import Button from '../components/Button'
import useCsvButton from './useCsvButton'
import { responsive } from '../components/ThemeProvider/theme'

import icons from './icons'

const BottomButton = forwardRef((props, ref) => {
  const buttons = useCsvButton()
  const [buttonBoxRef, { width }] = useMeasure()
  return (
    <Flex
      position={responsive('inherit', 'fixed')}
      flexDirection={responsive('row', 'column')}
      bottom={responsive('0', '3rem')}
      left={responsive('0', 'unset')}
      right={responsive('0', '1.625rem')}
      zIndex={99}
      ref={ref}
      {...props}
    >
      {get(buttons, ['data'], []).slice(1, 3).map(({ name, link, icon }, i) => (
        <Box width={responsive('50%', 'auto')} ref={buttonBoxRef} mt={i && responsive(0, '0.875rem')} key={i}>
          <Button
            width="100%"
            height={responsive('auto', `${width}px`)}
            px="0.25rem"
            as={Link}
            href={link}
            borderLeft={i && responsive('1px solid white', 'none')}
            fontSize="0.75em"
            borderRadius={responsive('0', '0.5rem')}
          >
            <Flex
              flexDirection={responsive('row', 'column')}
              alignItems="center"
            >
              {icons[icon] && (
                <Box mb={responsive(0, '0.5em')} mr={responsive('0.5em', 0)}>
                  {icons[icon]}
                </Box>
              )}
              {name}
            </Flex>
          </Button>
        </Box>
      ))}
    </Flex>
  )
})

export default BottomButton
